import React from 'react'
import { Header, Icon, Image, Divider, Button , Label} from 'semantic-ui-react'
import './App.css';



const HeaderContent = ({display}) => ( 
    <Header.Subheader  className='info' >
    {display}
    </Header.Subheader>)

const Skills =({display})=><Header.Subheader  className='info' >

   {display.map(skill=><Label basic color='blue' size='large' key={skill}>
        {skill}
   </Label>)}
  </Header.Subheader>


const Summary = ({summary}) => (
  
  <div className='content' >
    <Header as='h1' icon textAlign='center' inverted>
    
      <Image bordered src={summary.image} style={{width:'162px'}} circular />
        <Header.Content>{summary.name}</Header.Content>
          <HeaderContent  display={summary.role} />
          <Skills  display={summary.skills} />
          <HeaderContent  display={summary.phone} />
          <HeaderContent display={<a style={{color:'#f2711c', textDecoration:'underline'}}  href={`mailto:${summary.email}`}>{summary.email}</a>} />
    </Header>

    <div style={{textAlign:'center'}}>

    <Button link target='_blank' href={summary.twitter} color='twitter'>
      <Icon name='twitter' /> Twitter
    </Button>
    <Button link  target='_blank' href={summary.github} color='github'>
      <Icon name='github' /> GitHub
    </Button>
    <Button link  target='_blank' href={summary.linkedIn} color='linkedin'>
      <Icon name='linkedin' /> LinkedIn
    </Button>
    </div>

 

 
      
          
 
  </div>
)

export default Summary


